import React from 'react';
import {Col, Container, Image, Row} from "react-bootstrap";
import "../../assets/css/footer.css"
import LinkedIn from "../../assets/img/logo/linkedin.svg"
import Github from "../../assets/img/icon/github.svg"

export default function Footer() {
    const year = new Date().getFullYear();
    return (
        <Container className="w-100 border-top" fluid="true">
            <Row className="d-flex flex-row align-items-center w-100">
                <Col>
                    <Image src={LinkedIn} className="contact-logo"/>
                    <Image src={Github} className="contact-logo"/>
                    <Image src={LinkedIn} className="contact-logo"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <p className="text-white">
                        Hosted on Github Page | Build with ReactJS
                        <br/>
                        © {year} Ian Parcon
                    </p>
                </Col>
            </Row>
        </Container>

    )
}
